import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, compareJSON, fetchEncryptStream, isNotEmpty } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt, DropDwnPicker_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import { DateCalComponent_Cmpt } from "../../Inventory";
import { CardsHrm } from "./cardshrm";
import { SelectDropAccount_Cmpt } from "../../ComprobanteParametrizados";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const DropDwnPicker = DropDwnPicker_Cmpt();
const DateCalComponent = DateCalComponent_Cmpt();


const DropInput = SelectDropAccount_Cmpt();



const frmNm = "upd_employee"

const UpdAccount  = ({data, confirm}) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	let flds = [
		"dob",
		"name", 
		"address", 
		"hasW2", 
		"dependents",
		"w2"
	]
	


	const save  = async ()  => {
		let ffm = getStore(frmNm);
		let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )

		let bdyq2 = {
			query: "updateEmployeesHRG",
			params:{
				"employeeId": ffm["employeeId"],
				businessId:  getStore("profileUser")?.businessId
			},
			data2update: diff.data
		};
		
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			updStore(frmNm + "_bck", {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
		
	};

	
	let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )



	
	
	return (
		<div
		style={{
			maxHeight: "80vh",
			width: "690px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 25px"
		}}
		>
			<div className="_dsplFlx ">
				<p className="form_title">
					Actualizar Empleado
				</p>
				<div className="flexSpace"/>
				
			</div>

			<TextInput  
				init={getStore(frmNm)?.["name"]} 
				label={"Nombre"} 
				width={520} 
				updChanges={(e)=>updFrm("name",e)}
			/>
			<TextInput  
				init={getStore(frmNm)?.["address"]} 
				label={"Direccion"} 
				width={520} 
				updChanges={(e)=>updFrm("address",e)}
			/>
			<div className="_dsplFlx " style={{margin:"10px 10px 10px 0"}}>
				<div className="flexSpace"/>
				<div >
					<DateCalComponent  
						init={getStore(frmNm)?.["dob"]} 
						label={"Fecha de nacimiento"}
						updDate={(e)=>updFrm("dob",e)}
					/>
				</div>
				<div className="flexSpace"/>
				<div className="_dsplFlx " style={{marginTop: 24}}>
					<span className={`checklabel`}>
						{"Pagar con W2"}
					</span>
					<CheckBoxSlide
						initvalue={getStore(frmNm)?.["hasW2"]}
						keyCode={57}
						updChange={(e)=>updFrm("hasW2",e)}
					/>
				</div>
				<div className="flexSpace"/>	
				{getStore(frmNm)?.["hasW2"] ?
				<div style={{marginTop:16}}>
					<DropDwnPicker 
						init={getStore(frmNm)?.["dependents"]}
						lbl_empty={"Dependents"} 
						list={
							[
								{label: "0 dependent", id:"0"},
								{label: "1 dependent", id:"1"},
								{label: "2 dependents", id:"2"},
								{label: "3 dependents", id:"3"},
								{label: "4 dependents", id:"4"},
								
							]
						}
						direction={"right"}
						onSelect={(e)=>updFrm("dependents",e)} 
					/>
				</div>
			:null}
			</div>
			
		
			
			{getStore(frmNm)?.["hasW2"] ?
			<div style={{marginTop:19}}>
				<CardTaxW2 item={getStore(frmNm)?.w2} updFrm={updFrm}/>
			</div>
			:null}
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{diff?.hasChange ?
				<div className="card">
					<button onClick={save}>
						Actualizar
					</button>
				</div>
				:null}
			</div>

			
		</div>
	);
};

export default UpdAccount;





export const CardTaxW2  = ({ item, updFrm }) => {
	const [obs, set_obs] = useState(0);

	

	
	const updFrmTx = (f, v, k) => {
		let frm2 = item || getStore("global_account_params")?.w2_params;
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		
		updFrm("w2", frm2);
		set_obs(gen16CodeId())
	};

	
 
	return (
		<CardsHrm >
			<div className="iMTYiw hiKoiM">{"W2 Tax deduction"}</div>
			<div className="_dsplFlx w_100prc" style={{marginTop:19}} />
			
			<TaxDecuctionItem 
				label={"Federal Tax"}
				percentDefault={14.5}
				onChange={updFrmTx}
				keyFld={"federalTax"}
				item={item}
			/>
			<TaxDecuctionItem 
				label={"Social Security"}
				percentDefault={6.2}
				onChange={updFrmTx}
				keyFld={"socialSecurity"}
				item={item}
			/>
			<TaxDecuctionItem 
				label={"Medicare"}
				percentDefault={1.45}
				onChange={updFrmTx}
				keyFld={"medicareTax"}
				item={item}
			/>
			<TaxDecuctionItem 
				label={"Estate"}
				percentDefault={4.5}
				onChange={updFrmTx}
				keyFld={"stateTax"}
				item={item}
			/>
			<TaxDecuctionItem 
				label={"City"}
				percentDefault={2.2}
				onChange={updFrmTx}
				keyFld={"cityTax"}
				item={item}
			/>
		</CardsHrm>
	)
}



export const TaxDecuctionItem  = ({ label, item, onChange, keyFld,  percentDefault }) => {
	const [edit, setEdit] = useState(0);
	const [value, setValue] = useState(0);
	const handleInput = (v) => {
		setValue(v.target.value);
	};
	const handleKey = (v) => {
		if(v.keyCode===13){
			if(isNotEmpty(value)){
				let nn = item?.[keyFld] || {};
				nn["percent"] = value;
				onChange && onChange(keyFld,nn);
			}
			setEdit(false);
		}
	};
	let percentLbl =  value || item?.[keyFld]?.percent || percentDefault;

	
	
	return (
		<div className="_dsplFlx w_100prc">
			
			<div className="igLyC jyoObE pddBtm-12">
				<div className="txfnt_lfal">
				{label}
				</div>
			</div>
			{edit?
			<div className="igLyC jyoObE" >
				<input
					className="inpyt_tx"
					type={"number"}
					//aria-autocomplete="both"
					aria-haspopup="false"
					auto-capitalize="off"
					auto-complete="off"
					auto-correct="off"
					spell-check="false"
					onChange={handleInput}
					onKeyDown={handleKey}
				/>
			</div>
			:
			<div className="igLyC jyoObE" onClick={()=>setEdit(!edit)}>{percentLbl+"%"}</div>
			}
		</div>
	)
}








const AddExtraExpenses = ({data, confirm, item}) => {
	const [obs, set_obs] = useState(0);
	const [acc, setAcc] = useState({});
	let account = acc?.account || item?.account; 
	let sub_account = acc?.subAccount || item?.subAccount; 

	let accountD = getStore("account_ids")?.[account];
	let subaccountD = getStore("sub_account_ids")?.[sub_account];


	const addExtraAccount2List = () => {
		confirm && confirm(acc);
		CloseModal({id:data.modalID})
	}

	const handlePick = (acc,subacc) => {
		let nn= {
			"account":acc,
			"subAccount":subacc
		}
		setAcc(nn);
		set_obs(gen16CodeId())
	}


	
	
	const clear = () => {
		setAcc(null);
		set_obs(gen16CodeId())
	};

	return (
		<div style={{
			maxHeight: "80vh",
			minHeight: "60vh",
			width: "560px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Associar Cuenta
				</p>
				<div className="flexSpace"/>
				
			</div>
		{account ? 
			<div className="_dsplFlx " style={{margin: "18px 0"}}>
				<div className=" acc_dtls">
					<div className="_dsplFlx"   onClick={clear}>
						<div className="code" style={{color: "#646cff"}}>
							{accountD?.code} 
						</div>
						<div className="name">
							{accountD?.name}
						</div>
					</div>
					{subaccountD ?
						<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
							<div className="code" >
								{subaccountD?.subAccount} 
							</div>
							<div className="name">
								{subaccountD?. subAccountName}
							</div>
						</div>
						:null}
					<div className="flexSpace"/>
				</div>
			</div> :
			<DropInput
				width={420}
				handleClick={handlePick}
			/>
			}
			<div className="card">
				<button onClick={addExtraAccount2List}>
					Associar
				</button>
			</div>
			</div>
	)
}
